import React, {useEffect} from 'react';
import Checkbox from '@mui/material/Checkbox';
import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';
import DisplayComponent from "./components/DisplayComponent";
import {AllFeatureNameArray} from "./data/AllFeatureNameArray";
import Button from '@mui/material/Button';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import Typography from '@mui/material/Typography';

function App() {
    const MyCheckBox = ({name, index, flipFunc, checked}:{name:string, index:number, flipFunc:(event:any)=>any, checked:boolean}) => {
        return(
            <>
                <Grid item xs={4} sm={2}>
                    {name}
                    <Checkbox checked={checked} onChange={flipFunc} id={String(index)}/>
                </Grid>
            </>
        )
    }
    const nameArray:string[] = AllFeatureNameArray;
    const reducer = (state:boolean[], index:number) => {
        let nextState = state.slice();
        nextState[index] = !state[index]
        return nextState;
    }
    const [checkedState, dispatch] = React.useReducer(reducer, [true].concat(nameArray.slice(1,nameArray.length).map(()=>false)));
    const [onValueState, setOnValueState] = React.useState(checkedState.filter(v=> v).length);


    useEffect(
        ()=> {
            setOnValueState(checkedState.filter(v=> v).length);
        }, [checkedState]
    )



    const flipChangedState = (event:any) => {
        dispatch(
            Number(event.target.id)
        );
    }

    return (
        <>
            <Container fixed>
                <Accordion>
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                    >
                        <Typography>色関連</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <Grid container spacing={2}>
                            {nameArray.slice(1,11).map((name,index)=> <MyCheckBox name={name} index={index+1} flipFunc={flipChangedState} checked={checkedState[index+1]}/>)}
                        </Grid>
                    </AccordionDetails>
                </Accordion>
                <Accordion>
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                    >
                        <Typography>イニシャル</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <Grid container spacing={2}>
                            {nameArray.slice(11,20).map((name,index)=> <MyCheckBox name={name} index={index+11} flipFunc={flipChangedState} checked={checkedState[index+11]}/>)}
                        </Grid>
                    </AccordionDetails>
                </Accordion>

                <Accordion>
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                    >
                        <Typography>特徴</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <Grid container spacing={2}>
                            {nameArray.slice(20,63).map((name,index)=> <MyCheckBox name={name} index={index+20} flipFunc={flipChangedState} checked={checkedState[index+20]}/>)}
                        </Grid>
                    </AccordionDetails>
                </Accordion>
                <Accordion>
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                    >
                        <Typography>作品シリーズ</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <Grid container spacing={2}>
                            {nameArray.slice(64,114).map((name,index)=> <MyCheckBox name={name} index={index+64} flipFunc={flipChangedState} checked={checkedState[index+64]}/>)}
                        </Grid>
                    </AccordionDetails>
                </Accordion>
                <Accordion>
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                    >
                        <Typography>スキル</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <Grid container spacing={2}>
                            {nameArray.slice(114,nameArray.length).map((name,index)=> <MyCheckBox name={name} index={index+114} flipFunc={flipChangedState} checked={checkedState[index+114]}/>)}
                        </Grid>
                    </AccordionDetails>
                </Accordion>
                <Accordion>
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                    >
                        <Typography>全部表示</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <Grid container spacing={2}>
                            {nameArray.map((name,index)=> <MyCheckBox name={name} index={index} flipFunc={flipChangedState} checked={checkedState[index]}/>)}
                        </Grid>
                    </AccordionDetails>
                </Accordion>
                {nameArray.slice(0,1).map((name,index)=> <MyCheckBox name={name} index={index} flipFunc={flipChangedState} checked={checkedState[index]}/>)}

                選択した特徴数：{onValueState} <br/>
                [選択中]
                <Grid container spacing={2}>
                    {
                        nameArray.map((name,index)=> [name, index])
                            .filter(v => checkedState[Number(v[1])])
                            .map( v=> <Grid item xs={3} sm={2}>{v[0]}</Grid>)
                    }
                </Grid>
                <br></br>
                <Button variant="contained"
                        onClick={(event => {
                            checkedState.map((state, index) => state && dispatch(index));
                        })}
                >
                    選択を全て削除
                </Button>
                <DisplayComponent flagArray={checkedState} viewFeatureNameArray={nameArray}/>
            </Container>
        </>
    );
}

export default App;
