import Grid from "@mui/material/Grid";
import React from "react";
import {getTsumuData, TsumuData} from "../interfaces/TsumuData";
import {AllTsumuNameArray} from "../data/AllTsumuName";
import {FeatureToTsumuMap} from "../data/FeatureToTsumuMap";

/*
const getTsumuData = (name:string) : TsumuData =>{
    return {name: name}
}
*/

const getViewTsumuArray = (checkedStateArray: boolean[], allFeature: string[]) : (TsumuData | undefined)[] => {
    let selectFeature = checkedStateArray.slice().map((element, index)=> element ? allFeature[index] : "").filter(item => item !== "");
    let selectTsumuNameArrayArray = selectFeature.map(feature => FeatureToTsumuMap.get(feature));
    let tmpResultTsumuName: (string | undefined)[] = AllTsumuNameArray;

    selectTsumuNameArrayArray.forEach(
        (nameArray) => {
            tmpResultTsumuName = tmpResultTsumuName?.filter(tsumuName => tsumuName !==undefined && nameArray?.includes(tsumuName))
        }
    )
    tmpResultTsumuName = tmpResultTsumuName.map(name => getTsumuData(name)?.name)
    tmpResultTsumuName = Array.from(new Set(tmpResultTsumuName))
    const result = tmpResultTsumuName.map(name => getTsumuData(name)).filter(item => item !== undefined)
    return result;
}

const DisplayComponentItem = ({tsumuData}: { tsumuData: TsumuData | undefined })=> {
    return (
        <>
            <Grid item xs={4} sm={2}>
                <p>
                    <a href={tsumuData?.detailUrl}>
                        {tsumuData?.name}
                    </a>
                </p>
                <img width={70} height={70} src={tsumuData?.iconUrl} />
            </Grid>
        </>
    )
}
const DisplayComponent = (
    {
        flagArray,
        viewFeatureNameArray
    }: { flagArray: boolean[], viewFeatureNameArray: string[] }) => {
    return (
        <>
            <Grid container spacing={2}>
                {getViewTsumuArray(flagArray, viewFeatureNameArray)?.map(viewTsumuData => <DisplayComponentItem tsumuData={viewTsumuData}/>)}
            </Grid>
        </>
    )
}

export default DisplayComponent;